import React, {PureComponent} from 'react'
import {
  Modal,
  Row,
  Col,
  Form,
  message,
  Button
} from 'antd'
import {CsxFormSelect} from 'Form'
import {API_SHIPPERS_SHIPPERTYPES, API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
const FormItem = Form.Item

@connect(
  state => ({...state.warehouseShipperRelation, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
  }
  componentDidMount() {
    // 数据初始化
    const {modalData, update, userLoation} = this.props
    // 判断是新增还是编辑
    if (modalData) {
      let reqObj = {...modalData}
      // 特殊处理数据
      reqObj.shipperCode = {
        value: reqObj.shipperCode && reqObj.shipperCode.toString(),
        children: reqObj.shipperName && reqObj.shipperName.toString()
      }
      reqObj.warehouseCode = {
        value: reqObj.warehouseCode && reqObj.warehouseCode.toString(),
        children: reqObj.warehouseName && reqObj.warehouseName.toString()
      }
      update({modalData: {...reqObj}})
    } else {
      let warehouseCode = {
        value: userLoation.value,
        children: userLoation.children
      }
      update({modalData: {warehouseCode}})
    }
  }
  handleSubmit = (e) => {
    const {form, modalData, handleAddOrEdit} = this.props
    e.preventDefault()
    form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        let reqObj = {...values}
        for (let i in reqObj) {
          if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
            if (i == 'warehouseCode') {
              reqObj.warehouseName = reqObj[i].children
            }
            if (i == 'shipperCode') {
              reqObj.shipperName = reqObj[i].children
            }
            reqObj[i] = reqObj[i].value
          }
        }

        let result = {
          data: reqObj
        }, msg = '创建成功'
        // 判断是否是修改,加ID
        if (modalData && modalData.id) {
          result.data.id = modalData.id
          result.urlParams = [modalData.id]
          msg = '编辑成功'
        }
        handleAddOrEdit({...result}, () => {
          message.success(msg)
          const {handleGetList, searchParams, page, size} = this.props
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      }
    })
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowModal: false})
  }
  render() {
    const {modalTitle, form: {getFieldDecorator}, modalTuttonLoading, modalData} = this.props
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 6}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 14}
      }
    }
    return (
      <Modal
        title={modalTitle}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className='operationModal'
        footer={null}
      >
        <Form onSubmit={this.handleSubmit}>
          <FormItem
            {...formItemLayout}
            label="仓库"
          >
            {getFieldDecorator('warehouseCode', {
              initialValue: modalData.warehouseCode
            })(
              <CsxFormSelect
                dateApi={API_SHIPPERS_SHIPPERTYPES}
                antd={{disabled: true}}
                selectItem={{key: 'key', valueKey: 'value'}}
              />
            )}
          </FormItem>
          <FormItem
            {...formItemLayout}
            label="货主名称"
            hasFeedback
          >
            {getFieldDecorator('shipperCode', {
              initialValue: modalData.shipperCode,
              rules: [{
                required: true, message: '请填写!'
              }]
            })(
              <CsxFormSelect
                dateApi={API_WAREHOUSESHIPPERRELATIONS_SHIPPERTYPES}
                searchParams={{operationType: 'I'}}
                selectItem={{key: 'key', valueKey: 'value'}}
              />
            )}
          </FormItem>
          <Row
            type="flex"
            justify="center"
          >
            <Col>
              <Button style={{marginRight: '48px'}} onClick={this.handleCloseModal}>取消</Button>
              <Button type="primary" htmlType="submit" loading={modalTuttonLoading}>保存</Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



